import React from 'react';
import { graphql, Link } from 'gatsby';
import Img from 'gatsby-image';
import styled from 'styled-components';
import { config, animated, useSpring } from 'react-spring';
import Layout from '../components/layout';
import GridItem from '../components/grid-item';
import SEO from '../components/SEO';
import { ChildImageSharp } from '../types';
import Home from '../components/home';

type PageProps = {
  data: {
    projects: {
      nodes: {
        title: string;
        slug: string;
        cover: ChildImageSharp;
      }[];
    };
  };
};

const Area = styled(animated.div)`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: 35vw;

  @media (max-width: ${(props) => props.theme.breakpoints[2]}) {
    grid-template-columns: 1fr;
    grid-auto-rows: 60vw;
  }
`;

const Projects: React.FunctionComponent<PageProps> = ({
  data: { projects },
}) => {
  const pageAnimation = useSpring({
    config: config.slow,
    from: { opacity: 0 },
    to: { opacity: 1 },
  });

  return (
    <Layout color="#000">
      <SEO title="Projects | Bhavisha Patel" />
      <div className="back-home">
        <Link to="/" aria-label="Bhavisha, Back to Home">
          <Home />
        </Link>
      </div>
      <Area style={pageAnimation}>
        {projects.nodes.map((project) => (
          <GridItem
            key={project.slug}
            to={project.slug}
            aria-label={`View project "${project.title}"`}
          >
            <Img
              alt={project.title}
              fluid={project.cover.childImageSharp.fluid}
            />
          </GridItem>
        ))}
      </Area>
    </Layout>
  );
};

export default Projects;

export const query = graphql`
  query Projects {
    projects: allProjectsYaml {
      nodes {
        title
        slug
        cover {
          childImageSharp {
            fluid(quality: 95, maxWidth: 1200) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`;
/*
projects: allProjectsYaml(sort: { fields: project_order, order: ASC }) {
  */
